<template>
  <div id="wrapper" :class="{ toggled: isToggled }">
    <side-bar-app @toggle="isToggled = !isToggled" @filter="filterData" />
    <div class="container bs-docs-container width-100" style="height: 619px">
      <div class="container-fluid">
        <div class="row" style="height: 100%">
          <div
            class="col-md-12"
            role="main"
            style="height: 100%"
            id="table"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SideBarApp from '@/components/shared/SideBarApp.vue';
import { TabulatorFull as Tabulator } from 'tabulator-tables';
import 'tabulator-tables/dist/css/tabulator_bootstrap5.min.css';
import JsonData from '../assets/data/projects.json';

export default {
  name: 'ProjectsDashboard',
  data() {
    return {
      isToggled: false,
      table: null,
    };
  },
  mounted() {
    let data = JsonData;
    let filteredData = [];
    let stage = this.$route.query.stage;
    data.forEach((i) => {
      if (i.stage == stage) {
        filteredData.push(i);
      } else if (stage == '') {
        filteredData.push(i);
      }
    });
    this.table = new Tabulator('#table', {
      locale: true,
      langs: {
        'ru-ru': {
          data: {
            loading: '',
            error: '',
          },
          pagination: {
            page_size: ' ',
            page_title: ' ',
            first: '',
            first_title: ' ',
            last: '',
            last_title: ' ',
            prev: '.',
            prev_title: '. ',
            next: '.',
            next_title: '. ',
            all: '',
            counter: {
              showing: '',
              of: '',
              rows: '',
              pages: '',
            },
          },
          headerFilters: {
            default: ' ...',
            columns: {
              name: '  ...',
            },
          },
        },
      },
      data: filteredData,
      layout: 'fitColumns',
      responsiveLayout: 'hide',
      tooltips: true,
      addRowPos: 'top',
      history: true,
      pagination: 'local',
      paginationSize: 7,
      paginationCounter: 'rows',
      movableColumns: true,
      initialSort: [{ column: 'name', dir: 'asc' }],
      columns: [
        { title: '', field: 'developer', hozAlign: 'left' },
        { title: '', field: 'name', formatter: 'textarea' },
        { title: '', field: 'stage', hozAlign: 'center' },
        { title: '', field: 'status' },
        {
          title: ' ',
          field: 'regionalImportance',
          width: 90,
          sorter: 'date',
          hozAlign: 'center',
          formatter: 'tickCross',
        },
        {
          title: '- ',
          field: 'CSA',
          width: 90,
          hozAlign: 'center',
          formatter: 'tickCross',
          sorter: 'boolean',
        },
        {
          title: ' ',
          field: 'RegulatoryGuillotine',
          width: 90,
          formatter: 'tickCross',
        },
        { title: '', field: 'from', width: 130, formatter: 'textarea' },
        { title: '', field: 'OKVED', width: 130, formatter: 'textarea' },
      ],
    });

    this.table.on('rowClick', function (e, row) {
      let rowData = row.getData();
    });
  },
  methods: {
    filterData(stage) {},
  },
  components: {
    SideBarApp,
  },
};
</script>

<style>
@media (max-width: 767px) {
  .tile-wrap {
    padding: 0;
  }
}
</style>
